import goodsHeader from './goods-header'
import goodsSwipper from './goods-swipper'
import goodsInfo from './goods-info'
import goodsNumber from './goods-number'
import goodsSpec from './goods-spec'
import goodsInstructions from './goods-instructions'
import goodsEvaluate from './goods-evaluate'
import goodsShop from './goods-shop'
import goodsDetails from './goods-details'
import goodsAction from './goods-action'

export default {
  data () {
    return {
      swipperList: [],
      summary: [],     
      scrollY: 0,
			goodsId:this.$route.params.id,
    }
  },
  components: {
    goodsHeader,
    goodsSwipper,
    goodsInfo,
    goodsNumber,
    goodsSpec,
    goodsInstructions,
    goodsEvaluate,
    goodsShop,
    goodsDetails,
    goodsAction
  },
  methods: {
    scroll (e) {
      this.scrollY = e.y
    },
    goodsNumberChange (o) {
      
    }
  },
	created () {
	  this.request(this.api.DetailGoods,{goodsId:33}).then(res => {
	    if (res.code == 200) {
	      this.swipperList = res.data.goodsImgList;
	    }
	  });
	}
}



