import goodsCell from './common/goods-cell'
import goodsNumberPanel from './goods-number-panel'

export default {
  components: {
    goodsCell,
    goodsNumberPanel
  },
  methods: {
    updateGoodsNumber () {
      this.$mPopup({
        mask: true
      }, (h) => {
        return h(goodsNumberPanel, {
          on: {
            change: this.changeHandler
          }
        })
      })
    },
    changeHandler (o) {
      this.$emit('change', o)
    }
  }
}



