import goodsCell from './common/goods-cell'
import goodsSpecPanel from './goods-spec-panel'
export default {
  components: {
    goodsCell,
    goodsSpecPanel
  },
  methods: {
    updateGoodsSpec () {
      this.$mPopup({
        mask: true
      }, (h) => {
        return h(goodsSpecPanel)
      })
    }
  }
}



